    height: 20px;

    background-image: url('data:image/svg+xml;charset=utf8,%3Csvg viewBox=\'0 0 20 20\' xmlns=\'http://www.w3.org/2000/svg\'%3E%3Ccircle fill=\'none\' stroke=\'%23dee4ef\' stroke-width=\'1\' cx=\'10\' cy=\'10\' r=\'7\'/%3E%3C/svg%3E');
