}

.highlight
{
    margin-bottom: 1rem;
    padding: 1.25rem;

    border: 1px solid #eff2f7;
    border-radius: .375rem; 
    background-color: #212d3a;
}

.highlight pre
{
    margin-bottom: 0;
    padding: 1.25rem;
}

.hljs
{
    display: block;
    overflow-x: auto;

    padding: .5em;

    color: #dcdcdc;
}
.hljs pre code
{
    color: #dcdcdc;
}

.hljs-keyword,
.hljs-literal,
.hljs-symbol,
